export default {
	props: {
		options: {
			type: Object,
			default: function(e) {
				return {}
			}
		}
	},
	methods: {
		 tapTab(data){ //点击tab-bar
			 if(data===1){
				 if(this.options.complete ==1){
					 return;
				 }else{
					 var  uid = '';
					 if(this.$store.state.userInfo.userId){
					 	uid = this.$store.state.userInfo.userId;
					 }
					 if(this.$store.state.isLogin){
						  uni.request({
						 	url: this.websiteUrl +'/api/task/everydayTask/'+this.options.taskId, //去签到。
						 	method:"POST",
							header:{Authorization:this.$store.state.userInfo.loginToken},
						 	data:{userId:uid},
						 	success: (res) => {
						 		// console.log(res.data);
								if(res.data.status == 0){
									if(res.data.sign == 1 ){
										this.isActive = true;
									}else{
										this.isActive = false;
									}
									this.options.complete = res.data.sign;
									this.taskOperDesc = res.data.taskOperDesc;
									// console.log(this.options);
									this.$emit("changeData",{userPoints:res.data.userPoints,coutinueMark:res.data.coutinueMark});
								}
						 	}
						 });
					 }else{
						 uni.navigateTo({
						 	url: '../../../pages/login/login'
						 })
					 }
				 }
			 }else if(data===2){
			 	uni.switchTab({
			 		url: '../../../pages/tarbar/share/share'
			 	})
			 }else if(data===3){
				uni.switchTab({
				 	url: '../../../pages/tarbar/discover/discover'
				 })
			 }else if(data===0){
				 uni.navigateTo({
				 	url: '../../pages/login/login'
				 })
			 }
		}
		},
	data() {
			return {
				image_src:"",
				isActive:false,
				taskOperDesc:''
			}
		},
	created() {
			this.image_src ="../../../static/taskItem"+this.options.id+".png";
			if(this.options.complete == 1 ){
				this.isActive = true;
			}else{
				this.isActive = false;
			}
			this.taskOperDesc = this.options.taskOperDesc;
		}
}
